import React, { useEffect, useState } from 'react';
import { AppBar,Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, List, Paper,TextField,Typography } from '@mui/material';
import { asemat }from './asemat';
import { format} from 'date-fns';
import { fi } from 'date-fns/locale';

interface JunanAsemat {
  trainNumber: number
  departureDate: string
  operatorUICCode: number
  operatorShortCode: string
  trainType: string
  trainCategory: string
  commuterLineID: string
  runningCurrently: boolean
  cancelled: boolean
  version: number
  timetableType: string
  timetableAcceptanceDate: string
  timeTableRows: TimeTableRow[]
}

interface TimeTableRow {
  stationShortCode: string
  stationUICCode: number
  countryCode: string
  type: string
  trainStopping: boolean
  commercialStop?: boolean
  commercialTrack: string
  cancelled: boolean
  scheduledTime: string
  actualTime?: string
  differenceInMinutes: number
  causes: any[]
  trainNumber: number
  trainReady?: TrainReady
  liveEstimateTime?: string
  estimateSource?: string
  omaEtaisyys: number
  omaEstimate: number
}

interface TrainReady {
  source: string;
  accepted: boolean;
  timestamp: string;
}

interface Sijainti {
    latitude: number;
    longitude: number;
}


const Aloitus : React.FC<any> = () : React.ReactElement => {

    const [asematiedot, setAsematiedot] = useState<JunanAsemat[]>([]);

    const [junanSijainti, setJunanSijainti] = useState<Sijainti>();

    const [valittujuna,setValittujuna] = useState<number>();
    
    const [open, setOpen] = React.useState(false);

    let edellinenasema : string ="";

    let asematvalmis : boolean = false;

    const haeData = async () : Promise<void> => {
    const yhteys : Response = await fetch("https://rata.digitraffic.fi/api/v1/trains/latest/"+valittujuna);
    const data : any = await yhteys.json();
    setAsematiedot(data);
 
}

useEffect(() => {

    if (valittujuna)
    {
      updateJuna();
    }
 
},[valittujuna]);


useEffect(() => {

  if (asematiedot.length>0)
  {

  asematvalmis = true;
    const intervalId = setInterval(updateJuna, 10000); 
    return () => clearInterval(intervalId);
  }
},[asematiedot]);



useEffect(() => {
  document.body.style.backgroundColor  =  "#333333";
    setOpen(true);
  }, []);



const updateJuna = async () : Promise<void> => {
    let uusiSijainti : Sijainti = {latitude : 0,longitude : 0}
    const yhteys : Response = await fetch("https://rata.digitraffic.fi/api/v1/train-locations/latest/"+valittujuna);
    const data : any = await yhteys.json();  
        try {
            uusiSijainti = {
            latitude: data[0].location.coordinates[1],
            longitude: data[0].location.coordinates[0]
            };     
                setJunanSijainti(uusiSijainti);
        }
        catch { }
        if (asematvalmis)
        {
          asematiedot[0].timeTableRows.forEach(element => {
              const index = asemat.findIndex((asema) => asema.stationShortCode === element.stationShortCode);
              const asemaSijainti : Sijainti = {
                latitude: asemat[index].latitude,
                longitude: asemat[index].longitude
              };
              var geo = require('node-geo-distance');
              geo.haversine(uusiSijainti, asemaSijainti, function(dist : number) {
              let etaisyys = Math.round(dist/1000);     
              let aikaEro : number = 0;
              let nopeus : number = 0;
              const nykyAika = new Date();
              const asemanAika = new Date(element.scheduledTime);
              aikaEro = (asemanAika.getTime() - nykyAika.getTime())/(1000);
              nopeus = dist / 1000 / aikaEro*60*60;
              element.omaEstimate=nopeus;
              element.omaEtaisyys=etaisyys;
          })});
          setAsematiedot(asematiedot);
        }
        else
        {
          setAsematiedot([]);
          haeData();
        }
    
      
    

    

}



 const handleClickOpen = (e : any) => {
    e.preventDefault();
    setOpen(true);
  };

  const handleClose = (e : any) => {
    e.preventDefault();
   
    
    updateJuna();
    setOpen(false);
  };



  const handleValittujunaChange = (event : any) => {
    setValittujuna(event.target.value);
    setAsematiedot([]);
    asematvalmis = false;
  };

return (
   <>
    <div style={{backgroundColor : "#333333"}}>
    <AppBar elevation={0} style={{  position: "sticky",display: 'flex',flexDirection: 'column', alignItems: 'center', backgroundColor : "#333333", color : "#FFFFFF" }}>  
        {valittujuna ?
        <div>
          <Typography variant='h5' textAlign={"center"}>
          Juna
          </Typography> 
          <Typography variant='h3' textAlign={"center"} paddingBottom={"5px"}  onClick={handleClickOpen} style={{ cursor: 'pointer'}}>
            {valittujuna}          
          </Typography>
        </div>          
          :
             <Typography variant='h3' paddingBottom={"5px"}  onClick={handleClickOpen} style={{ cursor: 'pointer', paddingTop : "20px"}}>
            {"Valitse juna"}
          </Typography>
    
      }
    </AppBar>
      <Dialog open={open} onClose={handleClose}     
      style={{ transform: 'translate(0%, -10%)'}}>
        <DialogTitle>Ecodrive 2.0</DialogTitle>
        <DialogContent>
          <DialogContentText>
           Anna kulussaolevan junan numero
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Junanumero"
            type="number"
            fullWidth
            variant="standard"
            value={valittujuna}
            onChange={handleValittujunaChange}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Ok</Button>
        </DialogActions>
      </Dialog>


{asematiedot[0] ?
    <div style={{position: "relative", width : "100%", height : "100%", overflow : "auto", display: 'flex', flexDirection: 'column', alignItems: 'center', backgroundColor : "#333333", color : "#FFFFFF"  }}>



  {asematiedot[0].timeTableRows.map((asema,idx) => (
    asema.actualTime ?
    null
    :
    edellinenasema===asema.stationShortCode ?
        null 
        :
        asema.omaEstimate <0 ?
        null
        :         
          <List key={idx}>
          {asema.omaEtaisyys ?
            <Typography variant='h5' textAlign={'center'}>
              {asema.omaEtaisyys+ " km @"}
              {Math.round(asema.omaEstimate)+" km/h "}           
            </Typography>
              :
              <Typography variant='body1' textAlign={'center'}>ladataan junan sijaintia...</Typography>}
              <div style={{marginBottom : "50px" , backgroundColor : 
              asema.omaEstimate<200 ?
                "#00B451"
                :
                "#555555"
                , width :"100%", borderRadius: '5px', padding : "10px",alignSelf : "center", textAlign :"center"}}>
                <Typography variant='h4' padding="4px" textAlign={"center"}>
                  {edellinenasema=asema.stationShortCode}
                  {" " +
                    format(new Date(asema.scheduledTime),"HH:mm",{ locale: fi })
                  }
              </Typography>
              </div>
          </List>
      
   
   
  ))}


 <div style={{ height: "50px" }}></div>



<Typography padding="20px" variant='body1'>
Aikataulu- ja kulkutiedot perustuvat Fintrafficin Digitraffic-palvelun avoimeen dataan.
Lisenssi: CC 4.0 BY. <br/>Sovelluksen päiväys 26.9.2023
</Typography>          


 <div style={{ height: "100px" }}></div>

   {
    

<Paper sx={{ position: "fixed", width : "1", bottom: 0, display: 'flex', flexDirection: 'column', alignItems: 'center', paddingTop : "10px", paddingBottom : "10px", backgroundColor : "#00B451"}}>
 <Typography color="#FFFFFF" variant='h6'>
         Copyright
    </Typography>
   <Typography  color="#FFFFFF" variant="h6">
       © Henri Sallila 2023 ©
    </Typography>

    
</Paper>


   }
</div>
: 
 null

 }

    

   </div>
   

   </>
  );
}

export default Aloitus;